.modal_container {
    display: flex;
    flex-direction: column;
    position: relative;
    .close_btn{
        position: absolute;
        top: -.5%;
        right: -6.3%;
        border: 1px solid black;
        border-radius: 15px;
        background: red;
        color: white;
        margin: 0 auto;
        padding: 1% 2%;
    }

    img {
        width: 85%;
        align-self: center;
    }

    .display {
        // border: 1px solid rebeccapurple;
        text-align: center;
        font-size: 3.5rem;
    }

    p {
        font-size: 1.5rem;
        padding: 3%;
        text-align: center;
    }


    .checkout, .add_to_cart {
        color: black;
        border: 1px solid black;
        border-radius: 15px;
        width: 100%;
        margin-top: 2%;

    }

    .add_to_cart {
        border-color: dodgerblue;
        transition: all 0.3s ease-in-out;
        cursor: pointer;
        &:hover {
            background: dodgerblue;
            color: white;
            transition: all 0.3s ease-in-out;
            cursor: pointer;
        }
    }

    .checkout {
        border-color: green;
        transition: all 0.3s ease-in-out;

        &:hover{

            background: green;
            color: white;
            transition: all 0.3s ease-in-out;
        }
    }

    .total {
        font-size: 2.5rem;
        margin: 2% 0;
    }
    
}